  max-width: 200px;
  padding-block: 32px 16px;
  padding-inline: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

.card__image {
  width: 100%;
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: inherit;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.card__sku-code {
  color: #616070;
  text-align: left;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 1.4;
}

.card__grade {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
