  justify-content: center;
  align-items: center;
  padding: 12px 8px;
  font-weight: 700;
  line-height: 1;
  transition: background-color .15s ease-in-out, color .15s ease-in-out;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
